*{margin:0;padding:0}html,body,#root{position:relative;height:100%;min-height:100%}body{font-family:"Nunito Sans",sans-serif;-webkit-font-smoothing:antialiased;-moz-osx-font-smoothing:grayscale}

.router-view{padding:0 2rem;display:grid;grid-template-rows:auto 1fr}.router-view header{display:flex;justify-content:space-between;align-items:center;margin:2rem 0;grid-gap:1rem;gap:1rem}@media screen and (max-width: 375px){.router-view header{flex-direction:column;align-items:stretch}.router-view .flex-change{flex-direction:column}}

.input-wrapper{display:flex;align-items:center;flex:1 1 100%;width:100%;height:100%;border-radius:7px;background-color:var(--background-color);color:var(--color)}.input-wrapper i{margin:0 1.3rem;padding:0;font-size:1.2rem;color:inherit}.input-wrapper input{flex:1 1 100%;border:inherit;outline:inherit;background:inherit;color:inherit;width:inherit;height:inherit;padding:1rem 0}.input-wrapper input::-webkit-input-placeholder{color:inherit}.input-wrapper input:-ms-input-placeholder{color:inherit}.input-wrapper input::-ms-input-placeholder{color:inherit}.input-wrapper input::placeholder{color:inherit}

.hover-box{border-radius:7px;box-shadow:0px 0px 4px 0 rgba(0,0,0,0.1);overflow:hidden;width:100%;height:auto}.hover-box:hover{box-shadow:0px 0px 4px 2px rgba(0,0,0,0.1)}.hover-box:focus-within{box-shadow:0px 0px 4px 2px rgba(0,0,0,0.25)}

.select-custom{position:relative;border:inherit;outline:inherit;width:100%;height:100%;background-color:var(--background-color);color:var(--color);border-radius:7px;padding:0.8rem 1rem;cursor:pointer}

.scale{border-radius:7px;flex:1 1 100%;width:100%;height:auto}.scale:hover{transform:scale(1.02)}

.button-custom{border:inherit;outline:inherit;display:flex;align-items:center;justify-content:space-between;cursor:pointer;width:100%;background-color:var(--background-color);color:var(--color);padding:var(--padding)}.button-custom i{font-size:1rem;margin-right:0.8rem}

#layout{height:inherit;display:grid;grid-template-rows:auto 1fr}#layout .container{position:relative;width:100%;margin:0 auto;padding:0 2rem}

